import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ReactiveFormsModule, NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { debounceTime } from 'rxjs/operators';
import { Location } from '@angular/common';
import { Subject } from 'rxjs';

import { OperationService } from 'src/app/Services/operation.service';
import { Merchandise } from '../../Models/merchandise';
import { Client } from '../../Models/client';

@Component({
  selector: 'app-receipts',
  templateUrl: './receipts.component.html',
  styleUrls: ['./receipts.component.scss']
})
export class ReceiptsComponent implements OnInit {
  
  private _success = new Subject<string>();
  successMessage = ''
  selected:boolean
  formGroup: FormGroup
  submitted: boolean
  pageSize: number
  pageNumber: number
  year: number = new Date().getFullYear();
  month: number = new Date().getMonth() + 1;
  date: number = new Date().getDate();
  displayMonths = 2;
  placement = 'top';
  typeAlert = 'success';

  constructor(public operationService:OperationService,
          private formBuilder: FormBuilder,
          private location: Location) {
    this.selected = false; 
    this.submitted = false;
    this.pageNumber = 1;
    this.pageSize = 15;
    this.formGroup = this.formBuilder.group({
        orderNumber: ['', Validators.required],            
        codigo_producto: ['', Validators.required],
        referencia: ['', Validators.required],
        lote: ['', Validators.required],
        cantidad_buen_estado: ['', Validators.required],
        serial_container: ['', Validators.required],
        cantidad_mal_estado:'',
        fecha_caducidad: '',
        observaciones:'',
        codigo_ONU:'',
        nit:'',
        cliente:'',
        nombre:''
    });

    //Alertas
    this._success.subscribe(
        message => this.successMessage = message
      );
    this._success.pipe(
        debounceTime(5000)
      ).subscribe(
        () => this.successMessage = ''
      );
    //fin alertas
  }

  ngOnInit() {
  	this.listarClientes();
  }

  get f() { return this.formGroup.controls; }

  regresar(){
    this.location.back();
  }

  listarClientes(){
  	this.operationService.getClients()
  		.subscribe( res =>{
  			this.operationService.clientList = res as Client[];
  		});
    this.operationService.selectedClient = null;
  }

  listarClienteNit(formulario?:NgForm){
		if(formulario.value.searchNit){
			this.operationService.getClientByNit(formulario.value.searchNit)
				.subscribe( res =>{
          this.operationService.clientList = [];
				  this.operationService.clientList.push(res as Client);
				},
        (error):void =>{
          console.log("Error busqueda de Nit.");
          console.log(error);
        },
        ()=>{}
        );
		}else{
			this.listarClientes();
		}
		formulario.reset();
	}

   pickClient(_id:string){
   		this.operationService.getClient(_id)
   			.subscribe( res =>{
   				this.operationService.selectedClient = res as Client;
          this.selected = true;
          this.formGroup.controls['cliente'].setValue(this.operationService.selectedClient._id);
          this.formGroup.controls['nit'].setValue(this.operationService.selectedClient.nit);
          this.formGroup.controls['nombre'].setValue(this.operationService.selectedClient.nombre);
   			});
	}

  registrarEntrada(formulario?:FormGroup){
    let code;
    this.submitted = true;
    console.log(this.formGroup.controls['fecha_caducidad'].value)
    if (formulario.value.cantidad_mal_estado < 0) {
      this.formGroup.controls['cantidad_mal_estado'].setValue("");
      if(formulario.value.cantidad_buen_estado < 1){
        this.formGroup.controls['cantidad_buen_estado'].setValue("");
      }
      return;
    }else if(this.formGroup.invalid || formulario.value.cantidad_buen_estado < 1) {
      if(formulario.value.cantidad_buen_estado < 1){
        this.formGroup.controls['cantidad_buen_estado'].setValue("");
      }
      return;
    }else if(formulario.value.cliente){
      this.submitted = false;
      if(this.formGroup.controls['fecha_caducidad'].value){
        this.formGroup.controls['fecha_caducidad'].setValue(
          new Date(
            formulario.value.fecha_caducidad.year + '-' +
            formulario.value.fecha_caducidad.month + '-' +
            formulario.value.fecha_caducidad.day
          )
        );
      }else{
        this.formGroup.controls['fecha_caducidad'].setValue(new Date());
      }
  		this.operationService.postReceipt(formulario.value).subscribe(
        res=>{
          if (JSON.stringify({'status':'Receipt saved'}) === JSON.stringify(res)) {
            this._success.next("Registro satisfactorio")
          }else{
            this.typeAlert = 'danger';
            this._success.next("Error, Datos incorrectos")
          }
        },
        (error):void =>{
          console.log(error);
          this.typeAlert = 'danger';
          this._success.next("Error, Datos incorrectos");
        },
        ()=>{}
        );
      code = formulario.value.orderNumber;
	  }
    
	  formulario.reset();
    this.formGroup.controls['cliente'].setValue(this.operationService.selectedClient._id);
    this.formGroup.controls['nit'].setValue(this.operationService.selectedClient.nit);
    this.formGroup.controls['nombre'].setValue(this.operationService.selectedClient.nombre);
    this.formGroup.controls['orderNumber'].setValue(code);
  }

  cancelar(){
    this.selected = false;
    this.submitted = false;
    this.listarClientes();
    this.formGroup.controls['fecha_caducidad'].setValue("");
  }

}
