import { Injectable, Input } from '@angular/core';
import { Client } from '../Models/client';
import { Merchandise } from '../Models/merchandise';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class OperationService {
  clientList: Client[];
	selectedClient: Client;
	readonly indexRoute = 'http://localhost:3000/api/rest/sglam';
	readonly receiptRoute = 'http://localhost:3000/api/rest/sglam/entradas';
	readonly outletRoute = 'http://localhost:3000/api/rest/sglam/salidas';
   
  constructor(private http:HttpClient) {
    this.clientList = [];
    this.selectedClient = new Client();
   }

  getClients() {
  	return this.http.get( this.indexRoute );
  }

  getClient( _id: string) {
  	return this.http.get( this.indexRoute + `/${_id}` );
  }

  postClient( client: Client) {
  	return this.http.post( this.indexRoute, client);
  }

  putClient( client: Client) {
  	return this.http.post( this.indexRoute + `/${client._id}`, client);
  }

  deleteClient( _id: string) {
  	return this.http.delete( this.indexRoute + `/${_id}`);
  }

  getMerchandises( _id: string) {
    //this.http.patch
    return this.http.get( this.indexRoute + `/mercancias/${_id}` );
  }

  getClientByNit( nit: string) {
  	return this.http.get( this.indexRoute +'/search/'+ `${nit}`);
  }

  getReceipts( _id: string) {
  	return this.http.get( this.receiptRoute + `/${_id}` );
  }

  postReceipt( receipt: Merchandise) {
  	return this.http.put( this.receiptRoute + `/${receipt.cliente}`, receipt );
  }

  getOutlets( _id: string) {
  	return this.http.get( this.outletRoute + `/${_id}` );
  }

  postOutlet(outlet: Merchandise) {
  	return this.http.put( this.outletRoute + `/${outlet.cliente}`, outlet );
  }

}
