export class Client {
	_id:string;
    nombre:string;
	nit:string;
    responsable:string;
    telefono:string;
    mail:string;
    mercancia:any = [];
    entradas:any = [];
    salidas:any = [];

    constructor(nombre = '', nit = '', responsable = '',
    	telefono = '', mail = '', mercancia=[],entradas=[],salidas=[]){
        this.nombre = nombre;
        this.nit = nit;
        this.responsable = responsable;
        this.telefono = telefono;
        this.mail = mail;
        this.mercancia=mercancia;
        this.entradas=entradas;
        this.salidas=salidas;
    }

}
