import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { saveAs } from 'file-saver';
import { Location } from '@angular/common';

import { OperationService } from 'src/app/Services/operation.service';
import { Client } from 'src/app/Models/client';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss']
})
export class ReportsComponent implements OnInit {

  pageSize: number;
  pageSizeClient: number;
  pageNumber: number;
  
constructor(public operationService: OperationService,
      private location: Location) {
this.pageNumber = 1;
this.pageSize = 20;
this.pageSizeClient = 5;
 }

ngOnInit() {
  this.listarClientes();
}

regresar(){
  this.location.back();
}

listarClientes(){
  this.operationService.getClients().subscribe( res =>{
    this.operationService.clientList = res as Client[];
  });
  this.operationService.selectedClient = null;
}

listarBusqueda(formulario?:NgForm){
  if(formulario.value.searchNit){
    this.operationService.getClientByNit(formulario.value.searchNit)
      .subscribe( res =>{
        this.operationService.clientList = res as Client[];
        this.listarSalidas(this.operationService.clientList[0]._id);
      });
  }else{
    this.listarClientes();
  }
  formulario.reset();
}

 pickClient(_id:string){
     this.operationService.getClient(_id)
       .subscribe( res =>{
         this.operationService.selectedClient = res as Client;
       });
     this.listarSalidas(_id);
}

listarSalidas(id:string){
  this.operationService.getClient(id)
    .subscribe( res =>{
      this.operationService.selectedClient = res as Client;
      this.operationService.getOutlets(id)
        .subscribe( res =>{
          this.operationService.selectedClient.salidas = res;
        });
    });
}

SaveDemo() {
  let vector = document.getElementsByTagName("table");
  let file = new File(
      [('   ' + vector[1].innerText + '\n')],
      "test.xls" ,{ type: 'text/plain;charset=utf-8'}
    );
  saveAs(file);
}
}
