import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ReactiveFormsModule, NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import {debounceTime} from 'rxjs/operators';
import { Location } from '@angular/common';
import {Subject} from 'rxjs';

import { OperationService } from 'src/app/Services/operation.service';
import { Merchandise } from '../../Models/merchandise';
import { Client } from '../../Models/client';

@Component({
  selector: 'app-outlets',
  templateUrl: './outlets.component.html',
  styleUrls: ['./outlets.component.scss']
})
export class OutletsComponent implements OnInit{

  private _success = new Subject<string>();
  successMessage = ''
  selected:boolean
  formGroup: FormGroup
  submitted: boolean
  pageSize: number
  pageNumber: number
  mercancia: Merchandise[]
  typeAlert = 'success';

  constructor( public operationService: OperationService,
          private formBuilder: FormBuilder,
          private location: Location) { 
    this.selected = false;
    this.submitted = false;
    this.pageNumber = 1;
    this.pageSize = 15;    
    this.formGroup = this.formBuilder.group({
        orderNumber: ['', Validators.required],            
        codigo_producto: ['', Validators.required],
        referencia: ['', Validators.required],
        lote: ['', Validators.required],
        cantidad: ['', Validators.required],
        cliente: '',
        nit:'',
        nombre:'',
        fecha_caducidad:'',
        codigo_ONU:''
    });
  //Alerta
    this._success.subscribe(message => this.successMessage = message);
    this._success.pipe(
      debounceTime(5000)
    ).subscribe(() => this.successMessage = '');
  }

  ngOnInit() {
    this.listarClientes();
    this.mercancia=[];
  }

  onKeyCode(event: KeyboardEvent) { // with type info
    let codigoProducto: string = this.formGroup.controls['codigo_producto'].value;
    codigoProducto = codigoProducto.trim();
    let fecha: string = '';
    let lote: string = this.formGroup.controls['lote'].value;
    lote = lote.trim();
    lote = lote.toUpperCase();

    for (var i = 0; i < this.mercancia.length; ++i){
      if(this.mercancia[i].code == codigoProducto){
        this.formGroup.controls['referencia'].setValue(
          this.mercancia[i].referencia
        )
      }
      if(this.mercancia[i].lote == lote && this.mercancia[i].code == codigoProducto){
        fecha = this.mercancia[i].fecha_caducidad.toString();
        fecha = fecha.slice(0,10);
        this.formGroup.controls['fecha_caducidad'].setValue(
          fecha
        )
      }
    }
  }

  onKeyLote(event: KeyboardEvent) { // with type info
    let fecha: string
    let codigoProducto: string
    let lote: string

    codigoProducto = this.formGroup.controls['codigo_producto'].value;
    lote = this.formGroup.controls['lote'].value;
    codigoProducto = codigoProducto.trim();
    lote = lote.trim();
    lote = lote.toUpperCase();
    fecha = '';

    for (var i = 0; i < this.mercancia.length; ++i){
      if(this.mercancia[i].lote == lote && this.mercancia[i].code == codigoProducto){
        fecha = this.mercancia[i].fecha_caducidad.toString();
        this.formGroup.controls['fecha_caducidad'].setValue(
          fecha.slice(0,10)
        )
      }
    }
  }

  get f() { return this.formGroup.controls; }

  regresar(){
    this.location.back();
  }

  listarClientes(){
  	this.operationService.getClients()
  		.subscribe( res =>{
  			this.operationService.clientList = res as Client[];
  		});
    this.operationService.selectedClient = null;
  }
  
  listarClienteNit(formulario?:NgForm){
		if(formulario.value.searchNit){
			this.operationService.getClientByNit(formulario.value.searchNit)
				.subscribe( res =>{
          this.operationService.clientList = [];
          this.operationService.clientList.push(res as Client);
				},
        (error):void =>{
          console.log("Error busqueda de Nit.");
          console.log(error);
        },
        ()=>{}
        );
		}else{
			this.listarClientes();
		}
		formulario.reset();

	}

  pickClient(_id:string){
   this.operationService.getClient(_id).subscribe( 
     res =>{
       this.operationService.selectedClient = res as Client;
       
       this.selected = true;
       this.formGroup.controls['cliente'].setValue(
         this.operationService.selectedClient._id
        );
       this.formGroup.controls['nit'].setValue(
         this.operationService.selectedClient.nit
        );
       this.formGroup.controls['nombre'].setValue(
         this.operationService.selectedClient.nombre
        );
      }
    );
  }

  registrarSalida(formulario?:FormGroup){
    let order;
    this.submitted = true;
    if(this.formGroup.invalid || formulario.value.cantidad < 1){
      if (formulario.value.cantidad < 1) {
        this.formGroup.controls['cantidad'].setValue("");
      }
      return;
    }else if(formulario.value.cliente){
      let fecha: string

      this.submitted = false;
      if(this.formGroup.controls['fecha_caducidad'].value){
        fecha = this.formGroup.controls['fecha_caducidad'].value;
        this.formGroup.controls['fecha_caducidad'].setValue(
            new Date(
              fecha.slice(0,4) + '-' +
              fecha.slice(5,7) + '-' +
              fecha.slice(8,10)
            )
        );
      }else{
        this.formGroup.controls['fecha_caducidad'].setValue(
          new Date()
        );
      }
      this.operationService.postOutlet(formulario.value).subscribe(
        res =>{ 
          if (JSON.stringify({'status':'Outlet saved'}) === JSON.stringify(res)) {
            this._success.next("Registro satisfactorio");
          }else{
            this.typeAlert = 'danger';
            this._success.next("Error, Datos incorrectos");
          }
        },
        (error):void =>{
          console.log(error);
          this.typeAlert = 'danger';
          this._success.next("Error, Datos incorrectos");
        },
        ()=>{}
      );
      order = formulario.value.orderNumber;
    }
    formulario.reset();

    this.formGroup.controls['cliente'].setValue(
        this.operationService.selectedClient._id
      );
    this.formGroup.controls['nit'].setValue(
        this.operationService.selectedClient.nit
      );
    this.formGroup.controls['nombre'].setValue(
        this.operationService.selectedClient.nombre
      );
    this.formGroup.controls['orderNumber'].setValue(order);
  }

  obtenerMercancia(_id:string){
    this.operationService.getMerchandises(_id).subscribe( 
      res =>{
        console.log(res);
        this.mercancia = res as Merchandise[];
        console.log(this.mercancia);
      },
      (error):void =>{
        console.log("error de Mercancia")
        console.log(error)
      },
      ()=>{}
    );
  }

  cancelar(formulario?:FormGroup){
    this.selected = false;
    this.submitted = false;
    this.listarClientes()
    formulario.reset()
  }
}