import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManagerComponent } from './Components/manager/manager.component';
import { LoginGuard } from './Guard/login.guard';
import { ReceiptsComponent } from './Components/receipts/receipts.component';
import { OutletsComponent } from './Components/outlets/outlets.component';
import { SearchComponent } from './Components/search/search.component';
import { ReportsComponent } from './Components/reports/reports.component';
import { LoginComponent } from './Components/login/login.component';


const routes: Routes = [{path: 'manager', component: ManagerComponent ,canActivate:[LoginGuard] },
{path: 'receipts', component:ReceiptsComponent ,canActivate:[LoginGuard] },
{path: 'outlets', component:OutletsComponent, canActivate:[LoginGuard]},
{path: 'search', component:SearchComponent, canActivate:[LoginGuard] },
{path: 'reports', component:ReportsComponent, canActivate:[LoginGuard] },
{path: 'login', component:LoginComponent}];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
